import axios from "axios";
import { toast } from "react-toastify";
import {
  asyncActionError,
  asyncActionFinish,
  asyncActionStart,
} from "../../../app/async/asyncReducer";
import {
  CREATE_APPLY,
  DELETE_APPLY,
  FETCH_APPLY,
  LISTEN_APPLY,
  REJECT_APPLY,
  FETCH_DISTRICT,
  FETCH_COURT,
  FETCH_PROFESSION,
  UPDATE_APPLY,
  FETCH_MEDIATOR,
  FETCH_OFFICE,
  FETCH_PRINT,
  APPROVE_APPLY,
  ASSING_MEADIATR
} from "./applyConstants";
const url = "customer";
export function loadApply(data,type) {
  return async function (dispatch,getState) {
    let url = getState().auth.currentUser.role.toLowerCase();
    console.log(url);
    console.log(url)
    dispatch(asyncActionStart());
    await axios
      .get(`/Request/${url}s/all`, {
        params: { ...data },
      })
      .then((datas) => {

        console.log(datas.data.data, " All");

        dispatch({
          type: FETCH_APPLY,
          payload: datas.data.data.data,
          totalCount: datas.data.message,
        });
        dispatch(asyncActionFinish());
      })
      .catch((err) => {
        dispatch(asyncActionError(err.message));
        toast.info("Xta ba verdi");
      });
  };
}
